.intro_Loading_screen {
  animation: FadeUpnDown 3s alternate infinite;
  width: 100%;
  height: 100%;
  position: fixed;
  color: white;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 10vw;
  height: max-content;
  margin: auto;
  margin-top: 20%;
}

.intro_Loading_screen > div {
  animation: bouncing-loader 0.6s infinite alternate;
  width: 20px;
  height: 20px;
  margin-top: 6%;
  margin-right: 2%;
  border-radius: 50%;
  background-color: #a3a1a1;
  opacity: 1;
}

@keyframes FadeUpnDown {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
